import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { useToast } from '@/hooks/use-toast';
import { 
  Users, 
  Syringe, 
  Calendar, 
  Bell, 
  Search,
  Plus,
  Edit,
  Send,
  CheckCircle,
  Clock,
  UserCheck,
  FileText
} from 'lucide-react';

interface Patient {
  id: string;
  lineUserId: string;
  displayName: string;
  firstName: string;
  lastName: string;
  phone?: string;
  email?: string;
  registeredAt: string;
  status: 'pending' | 'completed' | 'cancelled';
}

interface VaccineType {
  id: string;
  name: string;
  doses: number;
  interval: number;
  description: string;
  available: boolean;
}

interface Appointment {
  id: string;
  patientId: string;
  patientName: string;
  vaccineId: string;
  vaccineName: string;
  doseNumber: number;
  totalDoses: number;
  appointmentDate: string;
  appointmentTime: string;
  status: 'scheduled' | 'completed' | 'cancelled' | 'no-show';
  notes?: string;
  createdAt: string;
  staffId: string;
}

const StaffDashboard = () => {
  const [activeTab, setActiveTab] = useState('patients');
  const [patients, setPatients] = useState<Patient[]>([]);
  const [appointments, setAppointments] = useState<Appointment[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedPatient, setSelectedPatient] = useState<Patient | null>(null);
  const [showAppointmentForm, setShowAppointmentForm] = useState(false);
  const { toast } = useToast();

  //  n8n workflow
  const vaccineTypes: VaccineType[] = [
    {
      id: 'flu',
      name: '',
      doses: 2,
      interval: 365,
      description: ' ',
      available: true
    },
    {
      id: 'hep_b',
      name: '',
      doses: 3,
      interval: 30,
      description: ' 3 :  1,  2 (1 ),  3 (6 )',
      available: true
    },
    {
      id: 'tetanus',
      name: '',
      doses: 3,
      interval: 30,
      description: ' 3 :  1,  2 (1 ),  3 ( 2  6 )',
      available: true
    },
    {
      id: 'shingles',
      name: '',
      doses: 2,
      interval: 90,
      description: ' 2   3 ',
      available: true
    },
    {
      id: 'hpv',
      name: '',
      doses: 3,
      interval: 30,
      description: ' 3 :  1,  2 (1 ),  3 (6 )',
      available: true
    },
    {
      id: 'pneumonia',
      name: '',
      doses: 2,
      interval: 60,
      description: ' 2   2 ',
      available: true
    },
    {
      id: 'chickenpox',
      name: '',
      doses: 2,
      interval: 30,
      description: ' 2   1 ',
      available: true
    },
    {
      id: 'rabies',
      name: '',
      doses: 5,
      interval: 3,
      description: ' 5 : ,  3,  7,  14,  28',
      available: true
    }
  ];

  //  LINE
  useEffect(() => {
    const mockPatients: Patient[] = [
      {
        id: '1',
        lineUserId: 'U1234567890',
        displayName: ' ',
        firstName: '',
        lastName: '',
        phone: '081-234-5678',
        registeredAt: '2024-01-15T10:30:00',
        status: 'pending'
      },
      {
        id: '2',
        lineUserId: 'U2345678901',
        displayName: ' ',
        firstName: '',
        lastName: '',
        phone: '082-345-6789',
        registeredAt: '2024-01-16T14:20:00',
        status: 'pending'
      },
      {
        id: '3',
        lineUserId: 'U3456789012',
        displayName: ' ',
        firstName: '',
        lastName: '',
        registeredAt: '2024-01-17T09:15:00',
        status: 'completed'
      }
    ];

    const mockAppointments: Appointment[] = [
      {
        id: 'apt-1',
        patientId: '3',
        patientName: ' ',
        vaccineId: 'covid-19',
        vaccineName: '-19',
        doseNumber: 1,
        totalDoses: 2,
        appointmentDate: '2024-01-20',
        appointmentTime: '10:00',
        status: 'completed',
        notes: ' ',
        createdAt: '2024-01-17T09:30:00',
        staffId: 'staff-001'
      }
    ];

    setPatients(mockPatients);
    setAppointments(mockAppointments);
  }, []);

  //  search term
  const filteredPatients = patients.filter(patient =>
    patient.displayName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    patient.phone?.includes(searchTerm)
  );

  // 
  const handleCreateAppointment = (formData: any) => {
    if (!selectedPatient) return;

    const newAppointment: Appointment = {
      id: `apt-${Date.now()}`,
      patientId: selectedPatient.id,
      patientName: selectedPatient.displayName,
      vaccineId: formData.vaccineId,
      vaccineName: vaccineTypes.find(v => v.id === formData.vaccineId)?.name || '',
      doseNumber: 1,
      totalDoses: vaccineTypes.find(v => v.id === formData.vaccineId)?.doses || 1,
      appointmentDate: formData.date,
      appointmentTime: formData.time,
      status: 'scheduled',
      notes: formData.notes,
      createdAt: new Date().toISOString(),
      staffId: 'staff-001'
    };

    setAppointments(prev => [...prev, newAppointment]);
    setShowAppointmentForm(false);
    setSelectedPatient(null);

    //  LINE
    sendLineNotification(selectedPatient, newAppointment);

    toast({
      title: "",
      description: ` ${selectedPatient.displayName} `
    });
  };

  //  LINE
  const sendLineNotification = (patient: Patient, appointment: Appointment) => {
    //  LINE Bot API
    console.log('Sending LINE notification:', {
      to: patient.lineUserId,
      message: ` !

 :
 : ${appointment.vaccineName}
 : ${appointment.appointmentDate}
 : ${appointment.appointmentTime}
 : ${appointment.doseNumber}/${appointment.totalDoses}

 
 : 02-xxx-xxxx`
    });

    toast({
      title: "",
      description: ` LINE  ${patient.displayName} `
    });
  };

  return (
    <div className="min-h-screen bg-background p-6">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-foreground mb-2">
             - 
          </h1>
          <p className="text-muted-foreground">
              
          </p>
        </div>

        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full grid-cols-4 max-w-2xl">
            <TabsTrigger value="patients" className="flex items-center gap-2">
              <Users className="h-4 w-4" />
              
            </TabsTrigger>
            <TabsTrigger value="appointments" className="flex items-center gap-2">
              <Calendar className="h-4 w-4" />
              
            </TabsTrigger>
            <TabsTrigger value="vaccines" className="flex items-center gap-2">
              <Syringe className="h-4 w-4" />
              
            </TabsTrigger>
            <TabsTrigger value="notifications" className="flex items-center gap-2">
              <Bell className="h-4 w-4" />
              
            </TabsTrigger>
          </TabsList>

          {/*  */}
          <TabsContent value="patients" className="mt-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Users className="h-5 w-5" />
                   LINE
                </CardTitle>
                <CardDescription>
                  - LINE Bot 
                </CardDescription>
                <div className="flex items-center gap-4">
                  <div className="relative flex-1 max-w-md">
                    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                    <Input
                      placeholder="..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="grid gap-4">
                  {filteredPatients.map((patient) => (
                    <div key={patient.id} className="flex items-center justify-between p-4 border rounded-lg">
                      <div className="flex items-center gap-4">
                        <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
                          <UserCheck className="h-6 w-6 text-primary" />
                        </div>
                        <div>
                          <h3 className="font-semibold">{patient.displayName}</h3>
                          <p className="text-sm text-muted-foreground">
                            {patient.phone || ''}
                          </p>
                          <p className="text-xs text-muted-foreground">
                            : {new Date(patient.registeredAt).toLocaleString('th-TH')}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center gap-3">
                        <Badge variant={patient.status === 'completed' ? 'default' : 'secondary'}>
                          {patient.status === 'pending' ? '' : 
                           patient.status === 'completed' ? '' : ''}
                        </Badge>
                        <Button
                          onClick={() => {
                            setSelectedPatient(patient);
                            setShowAppointmentForm(true);
                          }}
                          size="sm"
                          className="flex items-center gap-2"
                        >
                          <Plus className="h-4 w-4" />
                          
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/*  */}
          <TabsContent value="appointments" className="mt-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Calendar className="h-5 w-5" />
                  
                </CardTitle>
                <CardDescription>
                  
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid gap-4">
                  {appointments.map((appointment) => (
                    <div key={appointment.id} className="flex items-center justify-between p-4 border rounded-lg">
                      <div className="flex items-center gap-4">
                        <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
                          <Syringe className="h-6 w-6 text-primary" />
                        </div>
                        <div>
                          <h3 className="font-semibold">{appointment.patientName}</h3>
                          <p className="text-sm text-muted-foreground">
                            {appointment.vaccineName} -  {appointment.doseNumber}/{appointment.totalDoses}
                          </p>
                          <p className="text-xs text-muted-foreground">
                             {appointment.appointmentDate}  {appointment.appointmentTime}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center gap-3">
                        <Badge variant={
                          appointment.status === 'completed' ? 'default' :
                          appointment.status === 'scheduled' ? 'secondary' :
                          appointment.status === 'cancelled' ? 'destructive' : 'outline'
                        }>
                          {appointment.status === 'scheduled' ? '' :
                           appointment.status === 'completed' ? '' :
                           appointment.status === 'cancelled' ? '' : ''}
                        </Badge>
                        {appointment.status === 'scheduled' && (
                          <Button size="sm" variant="outline">
                            <Edit className="h-4 w-4" />
                          </Button>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/*  */}
          <TabsContent value="vaccines" className="mt-6">
            <div className="grid md:grid-cols-2 gap-6">
              {vaccineTypes.map((vaccine) => (
                <Card key={vaccine.id}>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <Syringe className="h-5 w-5" />
                      {vaccine.name}
                    </CardTitle>
                    <CardDescription>
                      {vaccine.description}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">:</span>
                        <span className="font-semibold">{vaccine.doses} </span>
                      </div>
                      {vaccine.interval > 0 && (
                        <div className="flex justify-between">
                          <span className="text-sm text-muted-foreground">:</span>
                          <span className="font-semibold">{vaccine.interval} </span>
                        </div>
                      )}
                      <div className="flex justify-between">
                        <span className="text-sm text-muted-foreground">:</span>
                        <Badge variant={vaccine.available ? 'default' : 'secondary'}>
                          {vaccine.available ? '' : ''}
                        </Badge>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          {/*  */}
          <TabsContent value="notifications" className="mt-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Bell className="h-5 w-5" />
                  
                </CardTitle>
                <CardDescription>
                   LINE 
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Alert>
                  <Bell className="h-4 w-4" />
                  <AlertDescription>
                      1 
                  </AlertDescription>
                </Alert>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>

        {/*  */}
        {showAppointmentForm && selectedPatient && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
            <Card className="w-full max-w-md">
              <CardHeader>
                <CardTitle> {selectedPatient.displayName}</CardTitle>
                <CardDescription>
                  
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={(e) => {
                  e.preventDefault();
                  const formData = new FormData(e.target as HTMLFormElement);
                  handleCreateAppointment({
                    vaccineId: formData.get('vaccine'),
                    date: formData.get('date'),
                    time: formData.get('time'),
                    notes: formData.get('notes')
                  });
                }} className="space-y-4">
                  <div>
                    <Label htmlFor="vaccine"></Label>
                    <Select name="vaccine" required>
                      <SelectTrigger>
                        <SelectValue placeholder="" />
                      </SelectTrigger>
                      <SelectContent>
                        {vaccineTypes.filter(v => v.available).map(vaccine => (
                          <SelectItem key={vaccine.id} value={vaccine.id}>
                            {vaccine.name} ({vaccine.doses} )
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <Label htmlFor="date"></Label>
                    <Input
                      type="date"
                      name="date"
                      required
                      min={new Date().toISOString().split('T')[0]}
                    />
                  </div>

                  <div>
                    <Label htmlFor="time"></Label>
                    <Input
                      type="time"
                      name="time"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="notes"> ()</Label>
                    <Textarea
                      name="notes"
                      placeholder="..."
                      rows={3}
                    />
                  </div>

                  <div className="flex gap-3 pt-4">
                    <Button type="submit" className="flex-1">
                      <CheckCircle className="h-4 w-4 mr-2" />
                      
                    </Button>
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => {
                        setShowAppointmentForm(false);
                        setSelectedPatient(null);
                      }}
                    >
                      
                    </Button>
                  </div>
                </form>
              </CardContent>
            </Card>
          </div>
        )}
      </div>
    </div>
  );
};

export default StaffDashboard;