import { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { useToast } from '@/hooks/use-toast';
import { Calendar, Users, Bell, FileSpreadsheet } from 'lucide-react';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { supabase } from '@/integrations/supabase/client';

interface PatientData {
  id: string;
  name: string;
  phone: string;
  email?: string;
  lineId?: string;
}

interface AppointmentData {
  patientId: string;
  patientName: string;
  date: string;
  time: string;
  vaccine: string;
  hospital: string;
  notes?: string;
  notificationDate?: string;
}

const PatientAppointmentManager = () => {
  const [patients, setPatients] = useState<PatientData[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [appointment, setAppointment] = useState<AppointmentData>({
    patientId: '',
    patientName: '',
    date: '',
    time: '',
    vaccine: '',
    hospital: '',
    notes: '',
    notificationDate: ''
  });
  const { toast } = useToast();

  //  Supabase  Sync  Google Sheets
  const loadPatients = async () => {
    setIsLoading(true);
    try {
      const { data, error } = await supabase
        .from('patient_registrations')
        .select('id, patient_name, phone_number, line_user_id, registration_id, created_at')
        .order('created_at', { ascending: false});

      if (error) throw error;

      const formattedPatients = data?.map(patient => ({
        id: patient.registration_id || patient.id,
        name: patient.patient_name,
        phone: patient.phone_number,
        lineId: patient.line_user_id,
        createdAt: patient.created_at
      })) || [];

      setPatients(formattedPatients);
      toast({
        title: "",
        description: ` ${formattedPatients.length}   Supabase`,
      });
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  // Sync  Google Sheets
  const syncPatientsToSheets = async () => {
    if (patients.length === 0) {
      toast({
        title: "",
        description: "",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const { data, error } = await supabase.functions.invoke('google-sheets-integration', {
        body: { 
          action: 'syncPatients',
          data: patients
        }
      });

      if (error) throw error;

      toast({
        title: "Sync ",
        description: `Sync  ${patients.length}   Google Sheets `,
      });
    } catch (error: any) {
      toast({
        title: "Sync ",
        description: error.message || " sync ",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  //  Supabase  Google Sheets
  const saveAppointment = async () => {
    if (!appointment.patientId || !appointment.date || !appointment.time || !appointment.vaccine) {
      toast({
        title: "",
        description: "",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      // 
      const selectedPatient = patients.find(p => p.id === appointment.patientId);
      if (!selectedPatient) {
        throw new Error('');
      }

      // 1.  appointments  Supabase ()
      const appointmentData = {
        patient_name: selectedPatient.name,
        patient_phone: selectedPatient.phone,
        patient_id_number: appointment.patientId,
        line_user_id: selectedPatient.lineId,
        vaccine_type: appointment.vaccine,
        appointment_date: appointment.date,
        appointment_time: appointment.time,
        status: 'scheduled',
        notes: appointment.notes || ` ${appointment.hospital}`,
        scheduled_by: 'staff_manual'
      };

      const { error: supabaseError } = await supabase
        .from('appointments')
        .insert([appointmentData]);

      if (supabaseError) throw supabaseError;

      // 2. Sync  Google Sheets ()
      try {
        await supabase.functions.invoke('google-sheets-integration', {
          body: { 
            action: 'saveAppointment',
            data: {
              ...appointment,
              patientName: selectedPatient.name,
              patientPhone: selectedPatient.phone,
              lineId: selectedPatient.lineId,
              timestamp: new Date().toISOString()
            }
          }
        });
        console.log(' Synced to Google Sheets successfully');
      } catch (syncError) {
        console.warn(' Failed to sync to Google Sheets:', syncError);
        //  sync error 
      }

      toast({
        title: "",
        description: ` ${selectedPatient.name}  ( sync )`,
      });

      // 
      setAppointment({
        patientId: '',
        patientName: '',
        date: '',
        time: '',
        vaccine: '',
        hospital: '',
        notes: '',
        notificationDate: ''
      });
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const sendNotification = async () => {
    if (!appointment.patientId || !appointment.date) {
      toast({
        title: "",
        description: "",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const selectedPatient = patients.find(p => p.id === appointment.patientId);
      if (!selectedPatient || !selectedPatient.lineId) {
        throw new Error(' LINE ID ');
      }

      const message = ` 

 ${selectedPatient.name}

 : ${appointment.date}
 : ${appointment.time}
 : ${appointment.vaccine}
 : ${appointment.hospital}

${appointment.notes ? ` : ${appointment.notes}` : ''}

`;

      const { error } = await supabase.functions.invoke('send-line-message', {
        body: {
          userId: selectedPatient.lineId,
          message: message
        }
      });

      if (error) throw error;

      toast({
        title: "",
        description: ` ${selectedPatient.name} `,
      });
    } catch (error: any) {
      toast({
        title: "",
        description: error.message || "",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handlePatientSelect = (patientId: string) => {
    const selectedPatient = patients.find(p => p.id === patientId);
    if (selectedPatient) {
      setAppointment(prev => ({
        ...prev,
        patientId,
        patientName: selectedPatient.name
      }));
    }
  };

  // Sync  Google Sheets
  const syncExistingAppointments = async () => {
    setIsLoading(true);
    try {
      //  Supabase
      const { data: appointments, error } = await supabase
        .from('appointments')
        .select('*')
        .order('created_at', { ascending: false })
        .limit(100); //  100 

      if (error) throw error;

      if (!appointments || appointments.length === 0) {
        toast({
          title: "",
          description: " sync",
          variant: "destructive",
        });
        return;
      }

      // Sync  Google Sheets
      const { error: syncError } = await supabase.functions.invoke('google-sheets-integration', {
        body: { 
          action: 'syncAppointments',
          data: appointments
        }
      });

      if (syncError) throw syncError;

      toast({
        title: "Sync ",
        description: `Sync  ${appointments.length}   Google Sheets `,
      });
    } catch (error: any) {
      toast({
        title: "Sync ",
        description: error.message || " sync ",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadPatients();
  }, []);

  return (
    <div className="space-y-4 md:space-y-6">
      {/*  */}
      <Card>
        <CardHeader className="pb-3 md:pb-4">
          <CardTitle className="flex items-center gap-2 text-lg md:text-xl">
            <Users className="h-5 w-5 text-primary" />
             Supabase
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4 p-4 md:p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            <Button onClick={loadPatients} disabled={isLoading} className="h-10 md:h-11">
              <FileSpreadsheet className="h-4 w-4 mr-2" />
              {isLoading ? '...' : ''}
            </Button>
            <Button 
              onClick={syncPatientsToSheets} 
              disabled={isLoading || patients.length === 0} 
              variant="outline" 
              className="h-10 md:h-11"
            >
              <FileSpreadsheet className="h-4 w-4 mr-2" />
              {isLoading ? ' Sync...' : 'Sync  Google Sheets'}
            </Button>
          </div>

          <Alert>
            <FileSpreadsheet className="h-4 w-4" />
            <AlertDescription>
              <strong> Dual Backup:</strong>  Supabase ()  sync  Google Sheets () 
              
            </AlertDescription>
          </Alert>
          
          {patients.length > 0 && (
            <div className="bg-muted/50 p-3 md:p-4 rounded-lg border">
              <p className="text-sm md:text-base font-medium mb-3">: {patients.length} </p>
              <div className="max-h-32 md:max-h-40 overflow-y-auto space-y-2">
                {patients.map((patient) => (
                  <div key={patient.id} className="text-xs md:text-sm bg-background p-2 md:p-3 rounded border border-border/30">
                    <div className="font-medium">{patient.name}</div>
                    <div className="text-muted-foreground">{patient.phone}</div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/*  */}
      <Card>
        <CardHeader className="pb-3 md:pb-4">
          <CardTitle className="flex items-center gap-2 text-lg md:text-xl">
            <Calendar className="h-5 w-5 text-primary" />
            
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4 p-4 md:p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="patient" className="text-sm md:text-base font-medium"></Label>
              <Select value={appointment.patientId} onValueChange={handlePatientSelect}>
                <SelectTrigger className="mt-1">
                  <SelectValue placeholder="" />
                </SelectTrigger>
                <SelectContent>
                  {patients
                    .filter(patient => patient.id && patient.id.trim() !== '')
                    .map((patient) => (
                      <SelectItem 
                        key={patient.id} 
                        value={patient.id}
                      >
                        {patient.name} - {patient.phone}
                      </SelectItem>
                    ))}
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="vaccine" className="text-sm md:text-base font-medium"></Label>
              <Select value={appointment.vaccine} onValueChange={(value) => 
                setAppointment(prev => ({ ...prev, vaccine: value }))
              }>
                <SelectTrigger className="mt-1">
                  <SelectValue placeholder="" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="COVID-19">COVID-19</SelectItem>
                  <SelectItem value=""></SelectItem>
                  <SelectItem value=" + "> + </SelectItem>
                  <SelectItem value="HPV">HPV</SelectItem>
                  <SelectItem value="MMR">MMR</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="date" className="text-sm md:text-base font-medium"></Label>
              <Input
                id="date"
                type="date"
                value={appointment.date}
                onChange={(e) => setAppointment(prev => ({ ...prev, date: e.target.value }))}
                className="mt-1"
              />
            </div>

            <div>
              <Label htmlFor="time" className="text-sm md:text-base font-medium"></Label>
              <Input
                id="time"
                type="time"
                value={appointment.time}
                onChange={(e) => setAppointment(prev => ({ ...prev, time: e.target.value }))}
                className="mt-1"
              />
            </div>
          </div>

          <div>
            <Label htmlFor="notes" className="text-sm md:text-base font-medium"></Label>
            <Textarea
              id="notes"
              placeholder=""
              value={appointment.notes}
              onChange={(e) => setAppointment(prev => ({ ...prev, notes: e.target.value }))}
              className="mt-1 min-h-[80px] md:min-h-[100px]"
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            <Button onClick={saveAppointment} disabled={isLoading} className="h-10 md:h-11">
              <Calendar className="h-4 w-4 mr-2" />
              {isLoading ? '...' : ''}
            </Button>
            <Button 
              onClick={syncExistingAppointments} 
              disabled={isLoading} 
              variant="outline" 
              className="h-10 md:h-11"
            >
              <FileSpreadsheet className="h-4 w-4 mr-2" />
              {isLoading ? ' Sync...' : 'Sync '}
            </Button>
          </div>
        </CardContent>
      </Card>

      {/*  */}
      <Card>
        <CardHeader className="pb-3 md:pb-4">
          <CardTitle className="flex items-center gap-2 text-lg md:text-xl">
            <Bell className="h-5 w-5 text-primary" />
            
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4 p-4 md:p-6">
          <div>
            <Label htmlFor="notificationDate" className="text-sm md:text-base font-medium"></Label>
            <Input
              id="notificationDate"
              type="date"
              value={appointment.notificationDate}
              onChange={(e) => setAppointment(prev => ({ ...prev, notificationDate: e.target.value }))}
              className="mt-1"
            />
          </div>

          <Alert>
            <Bell className="h-4 w-4" />
            <AlertDescription className="text-sm md:text-base">
               LINE  
            </AlertDescription>
          </Alert>

          <Button onClick={sendNotification} disabled={isLoading} className="w-full h-10 md:h-11">
            <Bell className="h-4 w-4 mr-2" />
            {isLoading ? '...' : ''}
          </Button>
        </CardContent>
      </Card>
    </div>
  );
};

export default PatientAppointmentManager;